import React from 'react';
import { Avatar, AvatarListItem, Button, Card, CardContent, Drawer, Grid, IconButton, ListItem, withStyles } from '@material-ui/core';
import TaskCard from './TaskCard';
import LoginPhoto from "../images/tasklogo.png";
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import { Link } from 'react-router-dom';

const styles = (theme) => ({

    large: {
        width: theme.spacing(7),
        height: theme.spacing(7),
      },
});

class LeftDrawer extends React.Component {


    constructor(props) {
        super(props);
        this.close = this.close.bind(this);
    }

    close(e) {
        console.log(e)
        console.log(this.props.toggleDrawer)
        this.props.toggleDrawer(false)(e);
    }

    render() {

        const { classes } = this.props;

        return (
            <div>

                <React.Fragment>
                    <Drawer open={this.props.open} onClose={this.close}>
                        <Card className="userSection">
                            <CardContent>
                                <Grid>

                                    <Avatar alt="David Vasquez" src={LoginPhoto} />
                                    <IconButton onClick={this.close}>
                                        <ChevronLeftIcon />
                                    </IconButton>
                                </Grid>
                            </CardContent>
                        </Card>

                        <div style={{ position: 'absolute', top: '90vh', margin: "auto" }}>
                            <ListItem>
                                <Link to="/">
                                    <Button type="button">
                                        <Avatar className={classes.large}>
                                            <ExitToAppIcon style={{ color: 'black' }} />
                                        </Avatar>
                                        Log out
                                    </Button>
                                </Link>
                            </ListItem>
                        </div>
                    </Drawer>
                </React.Fragment>

            </div>


        );
    }
}


export default withStyles(styles)(LeftDrawer);